@media (max-width:1300px) {

    section,
    header,
    footer {
        padding: 0 30px;
    }

    .categories {

        &__cards {
            width: 1100px;
        }

        &__card {
            width: 325px;
            min-width: 325px;
        }
    }
}

@media (max-width:1200px) {

    section,
    header,
    footer {
        padding: 0 15px;
    }

    .categories {

        &__cards {
            width: 950px;
        }

        &__card {
            width: 325px;
            min-width: 325px;
        }
    }
}


@media (max-width:993px) {

    section,
    header,
    footer {
        padding: 0 15px;
    }

    .categories {

        &__cards {
            width: 750px;
        }

        &__card {
            min-width: 300px;
            width: 300px;

            img {
                width: 100%;
            }
        }
    }

    .header {
        &__logo {
            img {
                width: 100px;
            }

        }

        &__nav {
            a {
                font-size: 16px;
            }
        }

        &__actions {
            gap: 15px;
        }
    }
}

@media (max-width:768px) {

    section,
    header,
    footer {
        padding: 0 15px;
    }

    .categories {

        &__cards {
            width: 400px;
            margin-left: 75px;
        }

        &__card {
            min-width: 400px;
            width: 400px;

            img {
                width: 100%;
            }
        }
    }

    #select1 {
        order: 1;
        font-size: 24px;
    }

    #select2 {
        display: none;
    }

    .header {

        &__wrapper {
            justify-content: space-around;
        }

        &__nav {
            display: none;
        }

        &__logo {
            order: 2;
        }

        &__burger {
            width: 50px;
        }

        &__actions {
            order: 3;
            margin-left: 50px;

            img {

                &:nth-child(1),
                &:nth-child(2) {
                    display: none;
                }
            }

        }
    }


    .sub {
        &__email {
            width: 100%;
        }

        .button {
            width: 100%;
        }

        &__text {
            width: 100%;
        }
    }

    .footer {

        &__wrapper {
            flex-wrap: wrap;
            align-items: center;
            padding: 0 0 30px 0;
        }

        &__block {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 30px;
            border-bottom: 1px solid rgb(143, 143, 143);
            text-align: center;

            &:last-child {
                border: none;
            }

            span {
                position: relative;
                width: 75%;
                text-align: center;

                &::after {
                    content: url('../img/footer/arrow.svg');
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transform: scale(2);
                    position: absolute;
                    width: 10%;
                    left: 90%;
                    top: -5px;
                    margin-left: 30px;
                }
            }


            div {
                max-height: 0;
                overflow: hidden;
                transition: all .5s ease;
            }

            a {
                display: block;
                width: 100%;
                font-size: 18px;
                height: 50px;
            }
        }

        &__socials {
            i {
                font-size: 30px;
            }

        }
    }
}

@media (max-width:578px) {

    .new {
        background-image: url('../img/header/phoneBG.jpg');
    }

    .categories {
        &__cards {
            width: 100%;
            margin-left: 0;
        }

        &__card {
            min-width: 100%;
            width: 100%;
        }
    }
}