.footer {
    margin-top: 100px;
    height: 200px;
    display: flex;
    align-items: center;

    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style: none;
    }

    &__block {
        span {
            display: block;
            font-size: 20px;
            font-weight: 400;
            margin-bottom: 20px;

        }

        a {
            font-size: 18px;
            font-weight: 300;
            color: #252525;

            &:hover {
                color: black;
            }
        }
    }

    &__socials {
        display: flex;
        gap: 15px;


        i {
            color: $accent;
        }
    }
}